import { Alert, Switch } from "@mui/material"
import { GridColDef, GridRenderCellParams } from "@mui/x-data-grid"
import React from "react"
import { Link } from "react-router-dom"

/**
 *      " ECHO"
 */
const headerTableSensors: GridColDef[] = [
  { field: "id", hide: true, type: "number" },
  {
    field: "name",
    headerName: "",
    type: "string",
    flex: 0.001,
    renderCell: (params): JSX.Element => (
      <Link to={`/sensors/${params.row.id}`}>{params.row.name}</Link>
    ),
  },
  {
    field: "ip",
    headerName: "IP  ()",
    type: "string",
    flex: 0.001,
  },
  {
    field: "equipment",
    headerName: "",
    type: "string",
    flex: 0.001,
  },
  {
    field: "status",
    headerName: " ",
    type: "boolean",
    flex: 0.001,
    renderCell: (params): JSX.Element =>
      params.value ? (
        <Alert severity="success"> </Alert>
      ) : (
        <Alert severity="error"></Alert>
      ),
  },
  {
    field: "record",
    headerName: " ",
    type: "boolean",
    flex: 0.001,
    renderCell: (params: GridRenderCellParams<boolean>): JSX.Element => (
      <Switch color="success" defaultChecked={params.value} />
    ),
  },
]

export { headerTableSensors }
