import { Button, Stack } from "@mui/material"
import React from "react"

import { BaseTextField, Cover } from "@components"
import { STextFieldName, STopStack, SDateRangePicker } from "./_styles"

/**
 *  " ",   "  "
 *
 * @returns {React.FC} - FC
 */
export const ModeDescription: React.FC = () => (
  <Cover
    title=" "
    autoHeight="autoHeight"
    indent={true}
    headerChildren={
      <Stack direction="row" spacing={2}>
        <Button variant="outlined"></Button>
        <Button variant="outlined"></Button>
      </Stack>
    }
  >
    <STopStack direction="row" spacing={8}>
      <STextFieldName
        title=""
        label=" "
        id="name"
        defaultValue="   "
      />
      <SDateRangePicker startText="" endText="" size="medium" />
    </STopStack>
    <Stack direction="row" spacing={8}>
      <BaseTextField
        title=""
        label=" "
        id="select-equipment"
        defaultValue="80% "
      />
      <BaseTextField
        title=""
        label=" "
        id="description"
        defaultValue="80% "
      />
    </Stack>
  </Cover>
)
