import { Button, Stack, styled, TextField } from "@mui/material"
import React from "react"

/**
 *  
 *
 * @param {object} otherData -   
 * @returns {React.FC} - FC
 */
export const SearchForm: React.FC = ({ ...otherData }) => (
  <Stack direction="row" alignItems="center" spacing={2} {...otherData}>
    <STextField type="search" size="small" placeholder="..." />
    <Button variant="contained"></Button>
  </Stack>
)

const STextField = styled(TextField)({
  width: "350px",
})
