import { Stack, TextField, Typography } from "@mui/material"
import { DatePicker, LocalizationProvider } from "@mui/lab"
import AdapterDateFns from "@mui/lab/AdapterDateFns"
import React, { ReactElement, useState } from 'react';

import { BaseTextField, Cover } from "@components"
import { STitleForm, SBaseTextField, SColumnForm } from "./_styles"

/**
 *  "  "
 *
 * @returns {React.FC} - FC
 */
export const ConnectingNewSensor: React.FC = () => {
  const [dateRecordingValue, setDateRecordingValue] = useState<Date | null>(
    null
  )

  return (
    <Cover
      title="  "
      autoHeight="autoHeight"
      indent={true}
    >
      <STitleForm variant="body1" children=" " />
      <Stack direction="row" spacing={5} alignItems="flex-start">
        <Stack direction="column" spacing={3}>
          <SBaseTextField title="" label=" " id="name" />
          <BaseTextField
            title=""
            label=" "
            id="password"
            type="password"
          />
        </Stack>
        <Stack direction="column" spacing={3}>
          <SColumnForm
            direction="row"
            spacing={3}
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography children=" " />
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <DatePicker
                label="   "
                value={dateRecordingValue}
                onChange={(newValue): void => setDateRecordingValue(newValue)}
                renderInput={(params): ReactElement => <TextField {...params} />}
              />
            </LocalizationProvider>
          </SColumnForm>
          <BaseTextField
            title=" "
            label=" "
            id="sensor-description"
          />
        </Stack>
        <Stack direction="column" spacing={3}>
          <BaseTextField
            title=""
            label=" "
            id="sensor-description"
          />
        </Stack>
      </Stack>
    </Cover>
  )
}
