import React, { Fragment } from "react"

import { IRowsVirtualSensorTable, ISelectMenuItemsList } from "@types"
import { DataTable } from "@components"
import { Filter } from "./_Filter"
import { headerTableSensors } from "./_constants"

interface IVirtualSensors {
  parameterList: ISelectMenuItemsList
  equipmentList: ISelectMenuItemsList
  rows: IRowsVirtualSensorTable
  setSelectionRows?: any
  height?: number
}

/**
 *   " "
 *
 * @param {ISelectMenuItemsList} parameterList -    
 * @param {ISelectMenuItemsList} equipmentList -    
 * @param {IRowsVirtualSensorTable} rows -  
 * @param {    } setSelectionRows - ,      
 * @param {number} height -   
 * @returns {React.FC<IVirtualSensors>} - FC
 */
export const VirtualSensors: React.FC<IVirtualSensors> = ({
  parameterList,
  equipmentList,
  rows,
  setSelectionRows,
  height,
}) => (
  <Fragment>
    <Filter parameterList={parameterList} equipmentsList={equipmentList} />
    <DataTable
      rows={rows}
      columns={headerTableSensors}
      setSelectionRows={setSelectionRows}
      height={height}
    />
  </Fragment>
)
