import { Typography } from "@mui/material"
import React from "react"

import { IconConnectionStatus } from "../icons"
import { SBox, SContent } from "./_styles"

interface IConnectionStatus {
  status: boolean
}

/**
 *     (/ )
 *
 * @param {boolean} status -  
 * @param {object} otherData -   
 * @returns {React.FC<IConnectionStatus>} - FC
 */
export const ConnectionStatus: React.FC<IConnectionStatus> = ({
  status,
  ...otherData
}) => (
  <SBox {...otherData}>
    <IconConnectionStatus status={status} />
    <SContent>
      <Typography variant="body1"> </Typography>
      {status ? (
        <Typography variant="h5" component="div" sx={{ color: "#3FBE5B" }}>
          
        </Typography>
      ) : (
        <Typography variant="h5" component="div" sx={{ color: "#F92F2F" }}>
           
        </Typography>
      )}
    </SContent>
  </SBox>
)
