type IGetIconStyle = {
  primary: string
  secondary: string
}

/**
 *         
 *
 * @param {boolean} isDark -  
 * @returns {IGetIconStyle} -      
 */
export const getIconStyle = (isDark?: boolean): IGetIconStyle =>
  isDark
    ? { primary: "#DB0B5B", secondary: "#FFF" }
    : { primary: "#898989", secondary: "#454443" }
