import React, { Fragment } from "react"

import { Breadcrumbs, SensorInstalling } from "@components"
import { exampleEquipments } from "./_constants"
import { ConnectingNewSensor } from "./fragments"

/**
 *  "  "
 *
 * @returns {React.FC} - FC
 */
const AddingVirtualSensorPage: React.FC = () => {
  const breadcrumbs = [" ", "  "]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ConnectingNewSensor />
      <SensorInstalling equipmentsList={exampleEquipments} />
    </Fragment>
  )
}

export default AddingVirtualSensorPage
