import { Button, Stack, Typography } from "@mui/material"
import React, { Fragment, useState } from "react"
import { Link } from "react-router-dom"

import { BasicModal } from "../Modal"

// Todo Egor:  
interface ITableEditingButtons {
  selectionRows: any[]
  outputField?: string
  addingButtonPatch?: string
  addingButtonDisabled?: boolean
  addingButtonHide?: boolean
  editButtonPatch?: string
  editButtonDisabled?: boolean
  editButtonHide?: boolean
  deleteButtonDisabled?: boolean
}

/**
 *      ,  
 *
 * @param {    } selectionRows -     
 * @param {string} outputField -   
 *
 * @param {string} addingButtonPatch -    ""
 * @param {boolean} addingButtonDisabled -    ""
 * @param {boolean} addingButtonHide -    ""
 *
 * @param {string} editButtonPatch -    ""
 * @param {boolean} editButtonDisabled -    ""
 * @param {boolean} editButtonHide -    ""
 *
 * @param {boolean} deleteButtonDisabled -    ""
 * @returns {React.FC} - FC
 */
export const TableEditingButtons: React.FC<ITableEditingButtons> = ({
  selectionRows,
  outputField = "name",
  addingButtonPatch = "",
  addingButtonDisabled = false,
  addingButtonHide = false,
  editButtonPatch = "",
  editButtonDisabled = selectionRows.length !== 1,
  editButtonHide = false,
  deleteButtonDisabled = !selectionRows.length,
}) => {
  const [isOpenModal, setIsOpenModal] = useState<boolean>(false)

  function eventIsOpenModal(): void {
    setIsOpenModal((prevState) => !prevState)
  }

  //      .    ,
  //   API
  function eventRemovingSensors(): void {}

  return (
    <Fragment>
      <Stack direction="row" spacing={2}>
        {!addingButtonHide && (
          <Link to={addingButtonPatch}>
            <Button variant="contained" disabled={addingButtonDisabled}>
              
            </Button>
          </Link>
        )}
        {!editButtonHide && (
          <Link to={editButtonPatch}>
            <Button variant="outlined" disabled={editButtonDisabled}>
              
            </Button>
          </Link>
        )}
        <Button
          variant="outlined"
          disabled={deleteButtonDisabled}
          onClick={(): void => eventIsOpenModal()}
        >
          
        </Button>
      </Stack>
      <BasicModal
        setIsOpenModal={(): void => eventIsOpenModal()}
        handleButtonSuccess={(): void => eventRemovingSensors()}
        isOpenModal={isOpenModal}
        title=" "
      >
        {selectionRows.map((row) => (
          <Typography variant="body1" children={row[outputField]} />
        ))}
      </BasicModal>
    </Fragment>
  )
}
