import React, { Fragment, useState } from "react"

import { IRowsSensorEchoTable, ISelectMenuItemsList } from "@types"
import { DataTable } from "@components"
import { Filter } from "./_Filter"
import { headerTableSensors } from "./_constants"

interface IEchoSensors {
  equipmentsList: ISelectMenuItemsList
  connectionStatusList: ISelectMenuItemsList
  recordsStatusList: ISelectMenuItemsList
  rows: IRowsSensorEchoTable
  setSelectionRows?: any
  height?: number
}

/**
 *   " Echo"
 *
 * @param {ISelectMenuItemsList} equipmentsList -  ,  
 * @param {ISelectMenuItemsList} connectionStatusList -   ,  
 * @param {ISelectMenuItemsList} recordsStatusList -   ,  
 * @param {IRowsSensorEchoTable} rows -   
 * @param {    } setSelectionRows - ,      
 * @param {number} height -   
 * @returns {React.FC<IEchoSensors>} - FC
 */
export const EchoSensors: React.FC<IEchoSensors> = ({
  rows,
  connectionStatusList,
  recordsStatusList,
  equipmentsList,
  setSelectionRows,
  height,
}) => {
  return (
    <Fragment>
      <Filter
        equipmentsList={equipmentsList}
        connectionStatusList={connectionStatusList}
        recordsStatusList={recordsStatusList}
      />
      <DataTable
        rows={rows}
        columns={headerTableSensors}
        height={height}
        setSelectionRows={setSelectionRows}
      />
    </Fragment>
  )
}
