import React, { Fragment } from "react"
import { useParams } from "react-router-dom"

import { Breadcrumbs, SensorRecordingTable } from "@components"
import { EventTable, ModuleDetails } from "./Fragments"

/**
 *   
 *
 * @returns {React.FC} - FC
 */
const SensorPage: React.FC = () => {
  const { id: urlParam } = useParams()
  const breadcrumbs = [" ", ` Echo ${urlParam}`]

  return (
    <Fragment>
      <Breadcrumbs breadcrumbs={breadcrumbs} />
      <ModuleDetails title={` Echo ${urlParam}`} />
      <EventTable />
      <SensorRecordingTable height={420} />
    </Fragment>
  )
}

export default SensorPage
