import { Stack, Typography } from "@mui/material"
import React, { ReactNode, InputHTMLAttributes } from "react"

import { STextField } from "./_styles"

interface ITextField extends InputHTMLAttributes<HTMLInputElement> {
  title: string
  label: string
  variant?: "standard" | "filled" | "outlined"
  id: string
  defaultValue?: string
  childrenStack?: ReactNode
}

/**
 *   TextField  
 *
 * @param {string} title -    ()
 * @param {string} label -    
 * @param {"standard" | "filled" | "outlined"} variant -   
 * @param {"password" | "text"} type -   
 * @param {string} id -  ID,   
 * @param {ReactNode} childrenStack -  ,    
 * @param {string} defaultValue -    
 * @param {boolean} disabled -   
 * @param {object} otherProps -   
 * @returns {React.FC<ITextField>} - FC
 */
export const BaseTextField: React.FC<ITextField> = ({
  title,
  label,
  variant = "outlined",
  type = "text",
  id,
  childrenStack,
  defaultValue,
  disabled,
  ...otherProps
}) => (
  <Stack direction="row" spacing={3} alignItems="center" {...otherProps}>
    <Typography variant="body1" children={title} />
    <STextField
      id={id}
      label={label}
      variant={variant}
      type={type}
      defaultValue={defaultValue}
      disabled={disabled}
    />
    {childrenStack}
  </Stack>
)
